import { VhAlgorithm, VhQuerySales, VhEcommerce } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from './../../../../../../../../services/language.service';
import { Component, OnInit, ChangeDetectorRef, Input, Output, EventEmitter, SimpleChanges } from '@angular/core';

@Component({
  selector: 'app-choose-product',
  templateUrl: './choose-product.component.html',
  styleUrls: ['./choose-product.component.scss']
})
export class ChooseProductComponent implements OnInit {
  listShop: Array<any> = [] // danh sách những của hàng sendo đã kết nối
  @Input() showDrawerSelectProduct: boolean
  @Input() listProductShow
  @Output() hideDrawerSelectProduct = new EventEmitter<any>()
  @Input() id_shop: any
  @Input() list_price_list: any
  listProduct: Array<any> = []
  listLocalProduct: Array<any> = []
  listSubProduct: Array<any> = []
  allChecked = false;
  indeterminate = false;
  heightScroll: any;
  id_shop_selected: string = '' //id shop đã chọn
  infoShop: any = {}
  setHeight = { "max-height": "500px", "height": "500px" }
  loadingDetail: boolean = false;
  sortPrice: 'asc' | 'desc' = 'asc'
  listPrice: any = {}
  searchValue: any;
  constructor(
    public lang: LanguageService,
    public vhComponent: VhComponent,
    public vhAlgorithm: VhAlgorithm,
    private vhEcommerce: VhEcommerce,
    private cdRef: ChangeDetectorRef,
  ) { }

  ngOnInit(): void {
    this.listShop = this.vhEcommerce.getlocalShopListShopee();
  }

  ngOnChanges({ listProductShow }: SimpleChanges): void {
    
    
    this.syncProductShopee();

  }

  /**Chọn shop và lọc sản phẩm theo shop đó */
  changeShop(id) {
    this.listProduct = [];
    this.listSubProduct = [];
    this.infoShop = this.vhEcommerce.getlocalShopListShopee().find(item => item._id == id)
    this.syncProductShopee()
  }

  /**Đem các biến thể ra đứng độc lập, mục đích dễ sort */
  syncProductShopee() {
    this.listProduct = []
    if (this.id_shop_selected.toString().length < 5 && this.id_shop_selected){ 
      for (let item of this.list_price_list[this.id_shop_selected].productlist) {
          if (item.model_id && (this.listProductShow.findIndex(i => i.model_id == item.model_id) == -1)) this.listProduct.push({ ...item ,status: false}) 
          if (!item.model_id && (this.listProductShow.findIndex(i => i.item_id == item.item_id) == -1)) this.listProduct.push({ ...item ,status: false}) 
         
      } 
    }
    else
    this.vhEcommerce.getlocalProductListShopee(this.infoShop['_id']).forEach(item => {
      if (item.item_status == 'NORMAL' && (this.listProductShow.findIndex(i => i.item_id == item.item_id) == -1)) {
        let data = {
          has_model: item.has_model, item_id: item.item_id, item_name: item.item_name,
          item_sku: item.item_sku, update_time: new Date(item.update_time * 1000), image: item.image, status: false
        }
        if (item.model && item.model.length) {
          if ((this.listProductShow.findIndex(i => i.model_id == item.model_id) == -1)) item.model.forEach(model => this.listProduct.push({
            ...data, ...model,
            price: model.price_info[0].original_price, newPrice1: model.price_info[0].original_price, newPrice2: model.price_info[0].original_price,
          }))
        }
        else
        if ((this.listProductShow.findIndex(i => i.item_id == item.item_id) == -1)) this.listProduct.push({
            ...data,
            price: item.price_info[0].original_price, newPrice1: item.price_info[0].original_price, newPrice2: item.price_info[0].original_price,
          })
      }
    })
    
    this.listSubProduct = this.listProduct
  }
  trackByFn(index, item) {
    return index;
  }

  addProduct() {
    this.hideDrawerSelectProduct.emit({ hide: false, productlist: this.listProduct.filter(item => item.status), id_shop: this.id_shop_selected.toString().length < 5 ? this.list_price_list[this.id_shop_selected].id_shop : this.id_shop_selected })
  }

  refreshStatus(): void {
    let validData = this.listProduct
    let allChecked = validData.length > 0 && validData.every(value => value.status === true);
    let allUnChecked = validData.every(value => !value.status);
    this.allChecked = allChecked;
    this.indeterminate = !allChecked && !allUnChecked;
  }

  checkAll(value: boolean): void {
    this.listProduct.forEach(data => {
      data.status = value;
    });
    this.refreshStatus();
  }

  ngAfterViewChecked() {
    if (document.querySelector("#by-product-sendo-price") && document.querySelector(".ant-table-thead") && document.querySelector(".by-product-sendo-price-header") && document.querySelector(".ant-table-pagination")) {
      this.heightScroll = document.querySelector("#by-product-sendo-price").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".by-product-sendo-price-header").clientHeight - 90 + "px";
    }
    this.cdRef.detectChanges();
  }
  searchProduct(value) {
    if (value.length) {
      let val: string = this.vhAlgorithm.changeAlias(value.trim().toLowerCase())
      this.listProduct = this.vhAlgorithm.searchList(val, [...this.listSubProduct], ['name'])
    } else this.listProduct = [...this.listSubProduct];
  }

  hide() {
    this.hideDrawerSelectProduct.emit({ hide: false, productlist: [], id_shop: this.id_shop ? this.id_shop : '' })
  }

  changeNewPrice1(item, value) {
    item.newPrice1 = value.toString().replaceAll(',', '')
  }
  changeNewPrice2(item, value) {
    item.newPrice2 = value.toString().replaceAll(',', '')
  }
}
