import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-modal-edit-operating-time',
  templateUrl: './modal-edit-operating-time.component.html',
  styleUrls: ['./modal-edit-operating-time.component.scss']
})
export class ModalEditOperatingTimeComponent implements OnInit {
  start: Date;
  @Input() showEditTime;
  @Input() otherTime: any
  @Input() data: any;
  @Input() day: any;
  @Output() hide = new EventEmitter<any>();
  @Output() delteTime = new EventEmitter<any>();
  @Output() edit = new EventEmitter<any>();
  listCheckTime: any = []
  constructor(
    private vhComponent: VhComponent,
    private languageService: LanguageService
  ) { }

  ngOnInit(): void {
    this.start = new Date(this.data ? this.data : new Date());
    

  }

  cancel() {
    this.hide.emit()
  }

  delete() {
    this.delteTime.emit({ day: this.day })
  }

  checkTime(arr, start) {
    for (let i in arr) {
      let startTime = new Date(arr[i]).getHours() + new Date(arr[i]).getMinutes() * 100;
      let startcheck = start.getHours() + start.getMinutes() * 100;

      if (startTime == startcheck) return true
    }
    return false
  }
  save() {
    if (this.start.getTime()) {
      if (!this.otherTime) this.edit.emit({ day: this.day, start: this.start })
      else {
        if (this.otherTime.getHours() != this.start.getHours() || (this.otherTime.getHours() == this.start.getHours() &&this.otherTime.getMinutes() != this.start.getMinutes())) {
          this.edit.emit({ day: this.day, start: this.start })
        }
        else this.vhComponent.showToast(2000, this.languageService.translate("Thời gian bạn chọn trùng với thời gian cập nhật khác"), "alert-toast")
      }
    }
    else this.vhComponent.showToast(2000, this.languageService.translate("The time start must be less than the time end"), "alert-toast")
  }


}
