import { ChooseTimeComponent } from './edit/choose-time/choose-time.component';
import { ModalEditOperatingTimeComponent } from './edit/modal-edit-operating-time/modal-edit-operating-time.component'; 
import { FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PriceListComponent } from './price-list.component'; 
import { EditComponent } from './edit/edit.component';
import { ChooseProductComponent } from './edit/choose-product/choose-product.component';

const routes: Routes = [
  {
    path: '',
    component: PriceListComponent
  },
   {
    path: 'edit',
    component: EditComponent
  }, 
]

@NgModule({
  declarations: [
    PriceListComponent, 
    EditComponent, ChooseProductComponent, ModalEditOperatingTimeComponent,ChooseTimeComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class PriceListModule { }
