import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { NzModalService } from 'ng-zorro-antd/modal';
import { LanguageService } from './../../../../../../services/language.service';
import { VhQuerySales, VhQuery } from 'ionic-vhframeworks';
import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-price-list',
  templateUrl: './price-list.component.html',
  styleUrls: ['./price-list.component.scss']
})
export class PriceListComponent implements OnInit {
  listPriceLists: any = [];
  showModalAdd = false;
  price_list_name = '';
  showModalEdit = false;
  id_price_list: any = ''
  constructor(
    private router: Router,
    private vhQuerySales: VhQuerySales,
    public languageService: LanguageService,
    private nzModalService: NzModalService,
    private vhQuery: VhQuery,
    private vhComponent: VhComponent
  ) { }

  ngOnInit(): void {
    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.getDocsByFields("ecommerce_price_lists",{type: {$eq:"shopee"}, id_branch : { $eq: this.vhQuerySales.getDefaultBranch()._id}})
        .then((response) => {
          
          
          this.listPriceLists = response; 
          for (let i in this.listPriceLists) {
            this.listPriceLists[i].time =  '';
            if (this.listPriceLists[i]['monday'].time1 || this.listPriceLists[i]['monday'].time2 ) this.listPriceLists[i].time = this.languageService.translate("Monday");
            if (this.listPriceLists[i]['tuesday'].time1 || this.listPriceLists[i]['tuesday'].time2) this.listPriceLists[i].time += ' - ' + this.languageService.translate("Tuesday");
            if (this.listPriceLists[i]['wednesday'].time1 || this.listPriceLists[i]['wednesday'].time2) this.listPriceLists[i].time += ' - ' + this.languageService.translate("Wednesday");
            if (this.listPriceLists[i]['thursday'].time1 || this.listPriceLists[i]['thursday'].time2) this.listPriceLists[i].time += ' - ' + this.languageService.translate("Thursday");
            if (this.listPriceLists[i]['friday'].time1 || this.listPriceLists[i]['friday'].time2) this.listPriceLists[i].time += ' - ' + this.languageService.translate("Friday");
            if (this.listPriceLists[i]['saturday'].time1 || this.listPriceLists[i]['saturday'].time2) this.listPriceLists[i].time += ' - ' + this.languageService.translate("Saturday");
            if (this.listPriceLists[i]['sunday'].time1 || this.listPriceLists[i]['sunday'].time2) this.listPriceLists[i].time += ' - ' + this.languageService.translate("Sunday");
          }
          this.vhComponent.hideLoading(0);
        })
    })

  }
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/auto-sale'])
  }

  addPriceList() {
    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.addDoc("ecommerce_price_lists", { name: this.price_list_name,type:'shopee', status: true, id_branch : this.vhQuerySales.getDefaultBranch()._id, monday: [], tuesday: [], wednesday: [], thursday: [], friday: [], saturday: [], sunday: [] })
        .then((response) => {
          this.vhComponent.hideLoading(0);
          this.listPriceLists.push(response);
          this.showModalAdd = false;
        }, error => {
          this.vhComponent.hideLoading(0);
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("An error while adding price list"))
        })
    })

  }

  editPriceList() {
    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.updateDoc("ecommerce_price_lists", this.id_price_list, { name: this.price_list_name }).then((bool) => {
        this.vhComponent.hideLoading(0);
        this.showModalEdit = false;
        this.listPriceLists[this.listPriceLists.findIndex(item => item._id == this.id_price_list)].name = this.price_list_name;
      }, error => {
        this.vhComponent.hideLoading(0);
        
      })
    })

  }

  gotoEdit(item) {
    this.router.navigate(['/sales/dashboard/manage/ecommerce/shopee/auto-sale/price-list/edit'], { state: {price_list:  item, list_price_list : this.listPriceLists} })
  }

  updateStatus(item) {
    item.status = !item.status
    this.vhComponent.showLoading('').then(() => {
      this.vhQuery.updateDoc("ecommerce_price_lists", item._id, { status: item.status }).then((bool) => {
        this.vhComponent.hideLoading(0);  
      }, error => {
        this.vhComponent.hideLoading(0);
        
      })
    })
  }

  deletePriceList(item, index) {

    this.nzModalService.confirm({
      nzTitle: this.languageService.translate("Are you sure to delete this price list"),
      nzCancelText: this.languageService.translate("Cancel"),
      nzOnOk: () => {
        this.vhComponent.showLoading('').then(() => {
          this.vhQuery.deleteDoc("ecommerce_price_lists", item._id)
            .then((bool) => {
              this.listPriceLists.splice(index, 1)
              this.vhComponent.hideLoading(0);
            })
        })
      },

    })
  }
}
