import { LanguageService } from './../../../../../../../../services/language.service';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-choose-time',
  templateUrl: './choose-time.component.html',
  styleUrls: ['./choose-time.component.scss']
})
export class ChooseTimeComponent implements OnInit {
  @Input() showDrawerSelectTime: boolean
  @Output() hideDrawerSelectTime = new EventEmitter<any>()
  @Input() priceList: any;
  showAddTime: boolean = false
  showEditTime: boolean = false
  day: number;
  data_time: any;
  otherTime: any;
  typeEdit: number; // 1 : thời gian giá 1| 2:  thời gian giá 2
  constructor(
    private vhComponent: VhComponent,
    private language: LanguageService
  ) { }

  ngOnInit(): void {
    this.initData()
  }

  initData() {
    this.priceList.monday = this.priceList.monday ? this.priceList.monday : []
    this.priceList.tuesday = this.priceList.tuesday ? this.priceList.tuesday : []
    this.priceList.wednesday = this.priceList.wednesday ? this.priceList.wednesday : []
    this.priceList.thursday = this.priceList.thursday ? this.priceList.thursday : []
    this.priceList.friday = this.priceList.friday ? this.priceList.friday : []
    this.priceList.saturday = this.priceList.saturday ? this.priceList.saturday : []
    this.priceList.sunday = this.priceList.sunday ? this.priceList.sunday : []
  }

  showModalEditTime(item, otherTime, type) {
    this.data_time = item;
    this.showEditTime = true;
    this.otherTime = otherTime
    this.typeEdit = type
  }

  hide() {
    this.hideDrawerSelectTime.emit({ hide: false })
  }


  addTime(day) {
    this.showAddTime = true;
    this.day = day;
    this.data_time = {}
  }
  deleteTime(ev) {
    
    switch (ev.day) {
      case 1: {
        if (this.typeEdit == 1) this.priceList.monday.time1 = '';
        else this.priceList.monday.time2 = '';
        break
      }
      case 2: {
        if (this.typeEdit == 1) this.priceList.tuesday.time1 = '';
        else this.priceList.tuesday.time2 = '';
        break
      }
      case 3: {
        if (this.typeEdit == 1) this.priceList.wednesday.time1 = '';
        else this.priceList.wednesday.time2 = '';
        break
      }
      case 4: {
        if (this.typeEdit == 1) this.priceList.thursday.time1 = '';
        else this.priceList.thursday.time2 = '';
        break
      }
      case 5: {
        if (this.typeEdit == 1) this.priceList.friday.time1 = '';
        else this.priceList.friday.time2 = ''
        break
      }
      case 6: {
        if (this.typeEdit == 1) this.priceList.saturday.time1 = '';
        else this.priceList.saturday.time2 = ''
        break
      }
      case 7: {
        if (this.typeEdit == 1) this.priceList.sunday.time1 = '';
        else this.priceList.sunday.time2 = ''
        break
      }
    }
    this.day = 0;
  }
  editoperating_time(ev) {
    
    
    switch (ev.day) {
      case 1: {
        if (this.typeEdit == 1) this.priceList.monday.time1 = ev.start;
        else this.priceList.monday.time2 = ev.start;
        break
      }
      case 2: {
        if (this.typeEdit == 1) this.priceList.tuesday.time1 = ev.start;
        else this.priceList.tuesday.time2 = ev.start;
        break
      }
      case 3: {
        if (this.typeEdit == 1) this.priceList.wednesday.time1 = ev.start;
        else this.priceList.wednesday.time2 = ev.start;
        break
      }
      case 4: {
        if (this.typeEdit == 1) this.priceList.thursday.time1 = ev.start;
        else this.priceList.thursday.time2 = ev.start;
        break
      }
      case 5: {
        if (this.typeEdit == 1) this.priceList.friday.time1 = ev.start;
        else this.priceList.friday.time2 = ev.start;
        break
      }
      case 6: {
        if (this.typeEdit == 1) this.priceList.saturday.time1 = ev.start;
        else this.priceList.saturday.time2 = ev.start;
        break
      }
      case 7: {
        if (this.typeEdit == 1) this.priceList.sunday.time1 = ev.start;
        else this.priceList.sunday.time2 = ev.start;
        break
      }
    }
    this.day = 0;
  }

  save() {
    this.hideDrawerSelectTime.emit({ hide: false })
  }
}
